body {
  width: 1000px;
  margin: 0 auto; }

h1, h2 {
  font-family: 'Poiret One'; }

h1 {
  font-size: 32px;
  margin: 30px 0 20px; }

h2 {
  font-size: 28px;
  margin: 22px 0 12px; }

ul.list {
  margin: 15px 10px 20px 20px;
  line-height: 1.2;
  list-style: disc; }

a:hover {
  text-decoration: none; }

.back-link {
  display: inline-block;
  padding: 7px 20px;
  margin: 10px 0;
  line-height: 20px;
  background: #eee;
  font-family: sans;
  font-size: 18px;
  color: #000;
  text-decoration: none; }
  .back-link:hover {
    background: #dedede; }

.embededvideo_page .videoembeded_component {
  margin: 30px 0; }
.embededvideo_page .embededvideo_page--collection_player {
  width: 500px;
  height: 250px;
  float: left; }
.embededvideo_page .embededvideo_page--playlist {
  margin: 30px 20px;
  float: left;
  width: 450px; }
  .embededvideo_page .embededvideo_page--playlist li {
    padding: 10px; }
  .embededvideo_page .embededvideo_page--playlist li:hover {
    background-color: #ddd;
    cursor: pointer; }
.embededvideo_page .videoembeded_component--play {
  width: 100px;
  height: 100px;
  background-color: red; }

.index_page--ul {
  line-height: 1.5; }

.index_page--li a {
  color: black; }

.active {
  font-weight: bold; }

.scroll_page--scroll1 {
  width: 400px;
  height: 200px; }
.scroll_page--scroll2 {
  width: 400px;
  height: 300px; }
  .scroll_page--scroll2 .scroller_content {
    width: 800px; }
.scroll_page--scroll3, .scroll_page--textarea {
  width: 400px;
  height: 200px;
  float: left; }

.swipe_page .gallery_loop, .swipe_page .gallery_simple {
  width: 1000px;
  height: 200px;
  padding: 20px 0;
  margin: 0 auto;
  background: salmon; }
.swipe_page .add_element_loop, .swipe_page .add_element_simple {
  margin: 10px 0;
  float: right;
  background: salmon;
  color: white;
  font-family: Arial;
  padding: 10px;
  cursor: pointer; }
.swipe_page .gallery_simple {
  background: #049fde; }
.swipe_page .add_element_simple {
  background: #049fde; }
.swipe_page .gallery_big {
  width: 1000px;
  height: 600px;
  padding: 20px 0;
  background: #D9B5A0; }
  .swipe_page .gallery_big .swipe_item {
    width: 800px; }
    .swipe_page .gallery_big .swipe_item:before {
      left: 100px;
      right: 100px; }
  .swipe_page .gallery_big .controls {
    margin: 40px auto;
    text-align: center; }
  .swipe_page .gallery_big .control {
    width: 10px;
    height: 10px;
    margin: 0 20px;
    border-radius: 100px;
    background-color: #aaa;
    display: inline-block; }
  .swipe_page .gallery_big .control.active {
    background-color: #000; }
  .swipe_page .gallery_big h1 {
    line-height: 600px; }

.swipe_item:before {
  content: " ";
  position: absolute;
  top: 0;
  left: 20px;
  right: 20px;
  height: 100%;
  background: white; }
.swipe_item h1 {
  position: relative;
  text-align: center;
  color: salmon;
  font-size: 70px;
  font-family: Arial;
  line-height: 200px;
  margin: 0; }

#modal-layout .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: auto;
  display: block;
  right: auto;
  bottom: auto;
  z-index: 101; }
#modal-layout .modal-header {
  text-align: center; }
  #modal-layout .modal-header .close {
    float: right; }
#modal-layout .modal-dialog {
  background: white;
  display: table;
  position: relative;
  min-width: 60%;
  margin: 10px auto; }
#modal-layout .modal-content {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  border-radius: 0;
  width: 100%;
  height: 100%;
  background: none;
  box-shadow: none;
  border: none; }
#modal-layout .modal-body {
  max-height: 100%;
  padding: 0; }
#modal-layout .modal-title {
  text-align: center; }

.modal-backdrop.in {
  opacity: 0.9;
  z-index: 100; }

.scroller_component {
  position: relative;
  overflow: hidden; }
  .scroller_component .scroller {
    width: 100%;
    height: 100%;
    border: 0; }
    .scroller_component .scroller[data-baron-v="inited"] {
      overflow-y: scroll;
      box-sizing: content-box;
      padding-right: 16px; }
      .scroller_component .scroller[data-baron-v="inited"] .scroller__track {
        right: 4px;
        top: 10px;
        bottom: 4px;
        width: 10px; }
      .scroller_component .scroller[data-baron-v="inited"] .scroller__bar {
        right: 0;
        width: 10px; }
    .scroller_component .scroller[data-baron-h="inited"] {
      overflow-x: scroll;
      box-sizing: content-box;
      padding-bottom: 16px; }
      .scroller_component .scroller[data-baron-h="inited"] .scroller__track {
        right: 4px;
        left: 4px;
        bottom: 4px;
        height: 10px; }
      .scroller_component .scroller[data-baron-h="inited"] .scroller__bar {
        height: 100%; }
    .scroller_component .scroller::-webkit-scrollbar {
      width: 0; }
    .scroller_component .scroller .scroller__track {
      display: none;
      position: absolute;
      background: rgba(0, 0, 0, 0.1); }
    .scroller_component .scroller.baron .scroller__track {
      display: block; }
    .scroller_component .scroller .scroller__bar {
      position: absolute;
      z-index: 1;
      background: #999; }

.swipegallery_component {
  width: 100%;
  height: 100%; }
  .swipegallery_component .swipe_gallery {
    position: relative;
    width: 100%;
    height: 100%; }
    .swipegallery_component .swipe_gallery > .ul_overflow {
      overflow: hidden;
      height: 100%;
      position: relative; }
      .swipegallery_component .swipe_gallery > .ul_overflow > ul {
        position: relative;
        height: 100%; }
        .swipegallery_component .swipe_gallery > .ul_overflow > ul.animate {
          transition: all .6s ease; }
        .swipegallery_component .swipe_gallery > .ul_overflow > ul > li {
          position: absolute; }
  .swipegallery_component .arrow_left, .swipegallery_component .arrow_right {
    user-select: none; }

.swipegallery_component--item {
  width: 330px;
  height: 100%; }

.videoembeded_component {
  position: relative;
  height: 480px;
  overflow: hidden; }
  .videoembeded_component iframe {
    width: 100%;
    height: 100%; }
  .videoembeded_component--image {
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover; }
  .videoembeded_component--play {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -55px;
    margin-left: -44px;
    cursor: pointer; }
